.intro-pricing{
    margin-top: 4em;
    margin-left: 2em;
    margin-right: 2em;
    margin-bottom: 15em;
}
.left-container{
    order: 2;
    position: relative;
    img{
        display: block;
        margin-top: 3em;
        margin-bottom: 3em;
        width: 100%;
    }
}
.mobile-h2{
    text-align: center;
    color: #444;
    font-family: 'Poppins Light';
    font-size: 2rem;
}
.pricing-page-body{
    display: flex;
    flex-direction: column;
}

.right-container{
    order: 1;
    margin-left: auto;
    margin-right: auto;
    h2{
        display: none;
    }
}
.button-arrow{
    display: flex;
    margin-top: 5em;
    align-items: center;
    justify-content: center;

    svg{
        margin-left: auto;
        margin-right: 3em;
    }
    button{
        display: block;
        border: none;
        background: none;

        a{
            text-decoration: none;
            color: #444;
            font-size: 1rem;
            font-weight: 500;
        }
    }
}

.more-about-prices{
    margin-top: 12em;
    margin-bottom: 15em;
}
.centered-containers{
    display: block !important;
    margin-left: auto !important;
    margin-right: auto !important;
}

.prices-section{
    p{
        text-align: center;
        font-size: 0.9rem;
    }
}


@media screen and (min-width: 780px) {
    .intro-pricing{
        display: flex;
        margin-top: 12em;
        margin-left: 2em;
        margin-right: 2em;
    }

    .mobile-h2{
        display: none;
    }
    .left-container{
        order: 1;
        width: 100%;
    }
    .right-container{
        width: 100%;
    }
.right-container{
    order: 2;
    h2{
        display: block;
        text-align: center;
        color: #444;
        font-family: 'Poppins Light';
        font-weight: 400;
        font-size: 2rem;
    }
    p{
        width: 90%;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }

    .button-arrow{
        display: flex;
        margin-top: 5em;
        align-items: center;
        justify-content: center;
        margin-right: 3em;

        svg{
            margin-left: auto;
            margin-right: 3em;
        }
        button{
            display: block;
            border: none;
            background: none;

            a{
                text-decoration: none;
                color: #444;
                font-size: 1rem;
                font-weight: 500;
            }
        }
    }
}
}
@media screen and (min-width:1490px) {
    .pricing-page-body{
        max-width: 1490px;
        margin-left: auto;
        margin-right: auto;
    }
}